.toggle {
		-webkit-transition: 0.2s;
		transition: 0.2s;
		position: fixed;
		z-index: 10;
		top: 0.3rem;
		right: 0.3rem;
		width: 2.5rem;
		height: 2.5rem;
		border-radius: 0.5rem;		
		background-color: var( --colour-text-dark );
}
.toggle:hover {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
}
#navbar .toggle span {
		position: absolute;
		left: 10%;
		right: 10%;
		height: .2rem;
		display: block;
		background-color: var( --colour-text-light );
}
#navbar .toggle .span-1 {
		top: 15%;
}
#navbar .toggle .span-2 {
		top: 45%;
}
#navbar .toggle .span-3 {
		top: 75%;
}
.bars .span-1 {
		-webkit-animation: anim_cross_to_flat_1 0.2s forwards;
		        animation: anim_cross_to_flat_1 0.2s forwards;
}
.bars .span-2 {
		-webkit-animation: anim_cross_to_flat_2 0.2s forwards;
		        animation: anim_cross_to_flat_2 0.2s forwards;
}
.bars .span-3 {
		-webkit-animation: anim_cross_to_flat_2 0.2s forwards;
		        animation: anim_cross_to_flat_2 0.2s forwards;
}
.cross .span-1 {
		-webkit-animation: anim_flat_to_cross_1 .2s forwards;
		        animation: anim_flat_to_cross_1 .2s forwards;
}
.cross .span-2 {
		-webkit-animation: anim_flat_to_cross_2 .2s forwards;
		        animation: anim_flat_to_cross_2 .2s forwards;
}
.cross .span-3 {
		-webkit-animation: anim_flat_to_cross_2 .2s forwards;
		        animation: anim_flat_to_cross_2 .2s forwards;
}
@-webkit-keyframes anim_flat_to_cross_1 {
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { top:45%; -webkit-transform: rotate(45deg); transform: rotate(45deg);
}
}
@keyframes anim_flat_to_cross_1 {
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { top:45%; -webkit-transform: rotate(45deg); transform: rotate(45deg);
}
}
@-webkit-keyframes anim_flat_to_cross_2 {
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { top:45%; -webkit-transform: rotate(135deg); transform: rotate(135deg);
}
}
@keyframes anim_flat_to_cross_2 {
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { top:45%; -webkit-transform: rotate(135deg); transform: rotate(135deg);
}
}
@-webkit-keyframes anim_cross_to_flat_1 {
0% { top:45%; -webkit-transform: rotate(45deg); transform: rotate(45deg);
}
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
}
@keyframes anim_cross_to_flat_1 {
0% { top:45%; -webkit-transform: rotate(45deg); transform: rotate(45deg);
}
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
}
@-webkit-keyframes anim_cross_to_flat_2 {
0% { top:45%; -webkit-transform: rotate(135deg); transform: rotate(135deg);
}
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
}
@keyframes anim_cross_to_flat_2 {
0% { top:45%; -webkit-transform: rotate(135deg); transform: rotate(135deg);
}
50% { top:45%; -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
}
