.panel {
	margin-bottom: 2rem !important;
	text-align: center;
	border-top-left-radius: 0.4rem; 
	border-top-right-radius: 0.4rem;
	overflow: hidden;		
	background-color: var( --colour-panel );
}

.panel header{
	padding: 0 1rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-bottom: solid 2px var( --colour-bg );
}

.panel header.no-split{
	min-height: .2rem;
	border-bottom: none;
}

.panel main{
	margin: 0.5rem var( --margin-screen-large );
}

.panel footer{
	min-height: 0.33rem;
	padding-bottom: 0.33rem;
	margin: 0 var( --margin-screen-large );
}

@media only screen and (max-width: 400px) {
	.panel main{
		margin: 0.5rem var( --margin-screen-med );
	}
	.panel footer{
		margin: 0 var( --margin-screen-med );
	}
}

@media only screen and (max-width: 320px) {
	.panel main{
		margin: 0.5rem var( --margin-screen-small );
	}
	.panel footer{
		margin: 0 var( --margin-screen-small );
	}
}
















































:root{
	--margin-screen-small: 0.5rem;
	--margin-screen-med: 1rem;
	--margin-screen-large: 1.5rem;

	--padding-screen-small: 0.5rem;
	--padding-screen-med: 0.8rem;
	--padding-screen-large: 1.25rem;	

	--btn-height-screen-small: 1.5rem;
	--btn-height-screen-med: 1.5rem;
	--btn-height-screen-large: 1.5rem;

	--row-height-screen-small: 1.75rem;
	--row-height-screen-med: 1.75rem;
	--row-height-screen-large: 1.75rem;
}


.no-click {
	pointer-events: none;
}


.no-wrap {
	white-space: nowrap;
	text-overflow: clip;
}


.width-95{
	width: 95%;
	max-width: 27rem;
}


.width-90{
	width: 90%;
	max-width: 25rem;
}


.width-85{
	width: 85%;
	max-width: 23rem;
}


.fill-all{
	position: fixed;
	left: -10rem;
	right: -10rem;
	top: -10rem;
	bottom: -10rem;
	z-index: 10;
}


.center-auto-h{
	margin: 0 auto;
}


.center-force-h{
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}


#app {
	margin:0; padding: 0;
}


body {
	margin: 0;
	margin-top: 4.5rem;
	padding: 0;
	background-color: var( --colour-bg );
}


body, footer, .footer {
	position: relative;
}


li{
	padding: .25rem;
}


button, .button {
	background-color: var( --colour-button );
	height: var( --btn-height-screen-large );
	border-radius: var( --border-radius-button );
	border: none;
	outline: none;
	position: relative;
	min-width: 4.5rem;
	-webkit-transition: background-color 1s, -webkit-transform .2s;
	transition: background-color 1s, -webkit-transform .2s;
	transition: transform .2s, background-color 1s;
	transition: transform .2s, background-color 1s, -webkit-transform .2s;
}


button:hover, .button:hover {
	-webkit-transform: scale(1.085);
	        transform: scale(1.085);
}


button, .button .text {
	text-transform: uppercase;
}


label, .label{
	-webkit-box-flex: 0;
	    -ms-flex: 0;
	        flex: 0;
	min-width: 5.5rem;
}


.field{
	text-align: left;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	margin: 0.75rem 0;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}


input, .input , form, .form{
	outline: none;
}


input ,.input{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border: 1px solid hsla(177,10%,50%,0.25);
	overflow: hidden;
	height: var( --row-height-screen-large );
}


input, .input {
	width: 100%;
	padding: 0 0.5rem !important;
	background-color: var( --colour-input );
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}


.shadow {
	box-shadow: 0 10px 10px var( --colour-shadow );
	-webkit-box-shadow: 0 10px 10px var( --colour-shadow );
	-moz-box-shadow: 0 10px 10px var( --colour-shadow );
}


.flex-row {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}


.flex-row-between {
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}


.flex-row-even {
	-webkit-box-pack: space-evenly;
	    -ms-flex-pack: space-evenly;
	        justify-content: space-evenly;
}


.flex-row-start {
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
}


.flex-row-end {
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
}


.flex-align-start {
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
}


.flex-align-center {
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}


.flex-align-end {
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
}


.flex-0{
	-webkit-box-flex: 0;
	    -ms-flex: 0;
	        flex: 0;
}


.flex-1{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}


