.fade-in {
	-webkit-animation: anim-fade-in 0.33s ease-in-out 1 backwards;
	        animation: anim-fade-in 0.33s ease-in-out 1 backwards;
}


@-webkit-keyframes anim-fade-in {
	0% { opacity: 0; -webkit-transform: scale(1.2); transform: scale(1.2); }
	100% { opacity: 1; -webkit-transform: scale(1); transform: scale(1); }
}


@keyframes anim-fade-in {
	0% { opacity: 0; -webkit-transform: scale(1.2); transform: scale(1.2); }
	100% { opacity: 1; -webkit-transform: scale(1); transform: scale(1); }
}


