:root {

	--colour-bg: hsla(177, 8%, 53%, 1.0);
	--colour-nav: hsla(177, 10%,35%, 1.0);
	/*--colour-nav: hsla(177, 2%, 40%, 1.0);
	--colour-nav-link: hsla(177, 2%, 40%, 1.0);
	--colour-alt-bg: hsla(177, 8%, 20%, 1.0);

	--colour-accent: hsla(158, 50%, 90%, 0.5);
	--colour-accent-hover: hsla(158, 50%, 90%, .75);*/
	--colour-panel: hsla(30, 10%, 96%, 1.0);
	
	--colour-input: hsla(177, 10%, 85%, 1.0);
	--colour-input-pre: hsla(177, 10%, 92%, 1.0);
	
	--colour-button: hsla(177, 8%, 20%, 1.0);

	--colour-shadow: hsla(177, 8%, 20%, 0.5);
	
	--colour-text-light: hsla(30, 10%, 96%, 1.0);
	--colour-text-dark: hsla(177, 8%, 20%, 1.0);

	--colour-text-positive: hsla(100, 40%, 50%, 1.0);
	--colour-text-negative: hsla(1, 40%, 50%, 1.0);
	
	--colour-success: hsla(100, 35%, 80%, 1.0);
	--colour-warning: hsla(50, 55%, 80%, 1.0);
	--colour-error: hsla(1, 35%, 80%, 1.0);

}

.colour-positive-bg {
	background-color: var( --colour-text-positive ) !important;
}

.colour-negative-bg {
	background-color: var( --colour-text-negative ) !important;
}

.colour-yellow {
	background-color: hsl(40,75%,55%) !important;
}

.shade {
	background-color: hsla(177, 8%, 20%, 0.75);
}














:root {

	--text-head-opacity: 0.75;

	--header-screen-large: 3.5rem;
	--header-screen-med: 3.5rem;
	--header-screen-small: 3.5rem;

	--title-screen-large: 1.66rem;
	--title-screen-med: 1.53rem;
	--title-screen-small: 1.4rem;

	--text-screen-large: 1.1rem;
	--text-screen-med: 1.1rem;
	--text-screen-small: 1rem;

	--text-is-small: 0.75;
}

text,p,h1,.header,.title,.text{
	margin: 0;
	padding: 0;
	font-weight: 300;
	color: var( --colour-text-dark );
	fill: var( --colour-text-dark );
	font-family: 'Roboto', sans-serif;	
}

.text-uppercase {
	text-transform: uppercase;
}

.text-blur {
	opacity: 0.4;
	font-weight: 700;
}

.text-funky { font-family: 'Fugaz One', cursive; }

.text-dark {
	fill: var( --colour-text-dark );
	color: var( --colour-text-dark );
}

.text-light {
	fill: var( --colour-text-light );
	color: var( --colour-text-light );
}

.text-positive {
	fill: var( --colour-text-positive );
	color: var( --colour-text-positive );
}

.text-negative {
	fill: var( --colour-text-negative );
	color: var( --colour-text-negative );
}

.text-left{ text-align: left; }

.text-center{ text-align: center; }

.text-right{ text-align: right; }

.text-med{	font-weight: 500 !important; }

.text-bold{	font-weight: 700 !important; }

.header {
	font-size: var( --header-screen-large );
}

.header.text-small {
	font-size: calc( var( --header-screen-large ) * var( --text-is-small ));
}

.title {
	line-height: 2.4rem;
	font-size: var( --title-screen-large );
}

.title.text-small{
	font-size: calc( var( --title-screen-large ) * var( --text-is-small ));
}

.text {
	font-size: var( --text-screen-large );
}

.text.text-small {
	font-size: calc( var( --text-screen-large ) * var( --text-is-small ));
}

.copyright {
	font-size: 0.7rem;
}

@media only screen and (max-width: 400px) {
	.header {
		font-size: var( --header-screen-med );
	}
	.header.text-small {
		font-size: calc( var( --header-screen-med ) * var( --text-is-small ));
	}
	.title {
		line-height: 2.1rem;
		font-size: var( --title-screen-med );
	}
	.title.text-small{
		font-size: calc( var( --title-screen-med ) * var( --text-is-small ));
	}
	.text {
		font-size: var( --text-screen-med );
	}
	.text.text-small {
		font-size: calc( var( --text-screen-med ) * var( --text-is-small ));
	}
	.copyright {
		font-size: 0.7rem;
	}
}

@media only screen and (max-width: 320px) {
	.header {
		font-size: var( --header-screen-small );
	}
	.header.text-small {
		font-size: calc( var( --header-screen-small ) * var( --text-is-small ));
	}
	.title {
		line-height: 1.8rem;
		font-size: var( --title-screen-small );
	}
	.title.text-small{
		font-size: calc( var( --title-screen-small ) * var( --text-is-small ));
	}
	.text {
		font-size: var( --text-screen-small );
	}
	.text.text-small {
		font-size: calc( var( --text-screen-small ) * var( --text-is-small ));
	}
	.copyright {
		font-size: 0.7rem;
	}
}
































