		-webkit-animation-delay: 0.1s;
		        animation-delay: 0.1s;
}
div button:nth-child(2), div .button:nth-child(2){
		-webkit-animation-delay: 0.2s;
		        animation-delay: 0.2s;
}
div button:nth-child(3), div .button:nth-child(3){
		-webkit-animation-delay: 0.3s;
		        animation-delay: 0.3s;
}
div button:nth-child(4), div .button:nth-child(4){
		-webkit-animation-delay: 0.4s;
		        animation-delay: 0.4s;
}
.button{
		overflow: hidden;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		border-radius: 0.3rem; 
		padding: 0 0.66rem;
		background-color: var( --colour-button );
}
.button p {
		-webkit-transition: 0.5s;
		transition: 0.5s;
}
.button.is-loading{
}
.button.is-loading p{
		opacity: 0.1;
}
.button.is-loading.is-success p{
		opacity: 0.75;
}
.button.is-loading.is-error p{
		opacity: 0.75;
}
.button .loading{
		position: absolute;
		top: -0.8rem;
		left: -1rem;
		z-index: 0;
		display: inline;
		height: 3rem;
		width: 1rem;		
		background-color: white;
		opacity: 0;
		-webkit-transition: 1s;
		transition: 1s;
		-webkit-transform: skew(20deg) translateX(-1rem);
		        transform: skew(20deg) translateX(-1rem);
}
@-webkit-keyframes is-loading-anim {
0% { -webkit-transform: skew(20deg) translateX(-.4rem); transform: skew(20deg) translateX(-.4rem);
}
100% { -webkit-transform: skew(20deg) translateX(6.6rem); transform: skew(20deg) translateX(6.6rem);
}
}
@keyframes is-loading-anim {
0% { -webkit-transform: skew(20deg) translateX(-.4rem); transform: skew(20deg) translateX(-.4rem);
}
100% { -webkit-transform: skew(20deg) translateX(6.6rem); transform: skew(20deg) translateX(6.6rem);
}
}
.button.is-loading .loading{
		opacity: 0.5;
		-webkit-animation: is-loading-anim 1.5s ease infinite;
		        animation: is-loading-anim 1.5s ease infinite;
}
.button.is-success .loading{
		opacity: 0.75;	
		-webkit-animation: is-loading-anim 1.5s ease infinite;	
		        animation: is-loading-anim 1.5s ease infinite;
}
.button.is-error .loading{
		opacity: 0.75;	
		-webkit-animation: is-loading-anim 1.5s ease infinite;	
		        animation: is-loading-anim 1.5s ease infinite;
}
.button .loading:nth-child(1){
		-webkit-animation-delay: 0;
		        animation-delay: 0;
}
.button .loading:nth-child(2){
		-webkit-animation-delay: 0.4s;
		        animation-delay: 0.4s;
}
.button .loading:nth-child(3){
		-webkit-animation-delay: 0.75s;
		        animation-delay: 0.75s;
}
.button.is-success{
		background-color: var( --colour-text-positive );
}
.button.is-error{
		background-color: var( --colour-text-negative );
}













































































































































































































































































